import MotionWrapper from "@/components/ui/MotionWrapper";
import Button from "@/components/ui/Button";
import Link from "next/link";
import {motion}  from "framer-motion";

export default function  About () {
    return (
        <section id="about" className="py-24 bg-muted/30">
            <div className="container px-4 md:px-6">
                <div className="grid gap-6 lg:grid-cols-2 lg:gap-12">
                    <MotionWrapper
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}>
                        <div>Обо мне</div>
                        <h2>Фронтенд разработчик с 5-летним опытом</h2>
                        <p>
                            Привет! Я Александр, фронтенд разработчик из Москвы. Я специализируюсь на создании современных
                            веб-приложений с использованием React, Next.js и TypeScript.
                        </p>
                        <p>
                            Мой путь в разработке начался 5 лет назад, и с тех пор я работал над различными проектами: от небольших
                            лендингов до крупных корпоративных приложений. Я стремлюсь создавать интуитивно понятные, доступные и
                            производительные интерфейсы.
                        </p>
                        <div>
                            <Button>
                                <Link href="#"></Link>
                            </Button>
                        </div>
                    </MotionWrapper>
                    <motion.div
                        className="space-y-4"
                        initial={{ opacity: 0, y: 20 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                        transition={{ duration: 0.5, delay: 0.2 }}
                    >
                        <h3 className="text-xl font-bold">Образование</h3>
                        <div className="rounded-lg border bg-card p-4">
                            <h4 className="font-semibold">Московский Государственный Университет</h4>
                            <p className="text-sm text-muted-foreground">Магистр компьютерных наук, 2018</p>
                        </div>
                        <h3 className="text-xl font-bold mt-6">Сертификаты</h3>
                        <div className="space-y-2">
                            <div className="rounded-lg border bg-card p-4">
                                <h4 className="font-semibold">Advanced React and Redux</h4>
                                <p className="text-sm text-muted-foreground">Udemy, 2020</p>
                            </div>
                            <div className="rounded-lg border bg-card p-4">
                                <h4 className="font-semibold">TypeScript Professional</h4>
                                <p className="text-sm text-muted-foreground">Frontend Masters, 2021</p>
                            </div>
                            <div className="rounded-lg border bg-card p-4">
                                <h4 className="font-semibold">Next.js Advanced Features</h4>
                                <p className="text-sm text-muted-foreground">Vercel, 2022</p>
                            </div>
                        </div>
                    </motion.div>
                </div>
            </div>
        </section>
    )
}
